// src/Navbar.js
import React from 'react';
import { Layout, Menu } from 'antd';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom for navigation
import { GoldOutlined } from '@ant-design/icons'; // Import Gold icon from @ant-design/icons

const { Header } = Layout;

const Navbar = () => {
  return (
    <Layout>
      <Header style={{ backgroundColor: 'black' }}> {/* Set background color of Header to black */}
        <Menu
          mode="horizontal"
          defaultSelectedKeys={['1']}
          style={{
            display: 'flex', 
            justifyContent: 'center',
            backgroundColor: 'black',  // Set Menu background to black
            border: 'none'  // Remove border (optional)
          }}
        >
            {/* Home menu item replaced with Gold icon and text aligned to the left */}
            <Menu.Item key="1" style={{ position: 'absolute', left: 0, color: 'white' }}>
                <Link to="/" style={{ color: 'white', display: 'flex', alignItems: 'center' }}>
                <GoldOutlined style={{ fontSize: '25px', color: 'red', marginRight: '1px' }} /> {/* Gold icon */}
                <span style={{ fontSize: '14px', fontWeight: 'bold', color: 'white', fontFamily: 'Montserrat, sans-serif' }}>
                    Decentralized Gold
                </span> {/* Text after icon */}
                </Link>
          </Menu.Item>

          {/* Other menu items will be centered with customized font */}
          <Menu.Item key="2" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>
            <Link to="/philosophy" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>Philosophy of Gold</Link>
          </Menu.Item>
          <Menu.Item key="3" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>
            <Link to="/Jewelry" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>Jewelry and Luxury Corner</Link>
          </Menu.Item>
          <Menu.Item key="4" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>
            <Link to="/Goldstandard" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>Why Gold Standard Catashtropic</Link>
          </Menu.Item>
          <Menu.Item key="5" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>
            <Link to="/Heaven" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>Gold as Heaven</Link>
          </Menu.Item>
          <Menu.Item key="6" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>
            <Link to="/Realvalue" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>Real Value of Gold</Link>
          </Menu.Item>
          <Menu.Item key="7" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>
            <Link to="/cryptocurrency" style={{ color: 'white', fontFamily: 'Arial, sans-serif', fontSize: '12px', fontWeight: 'bold' }}>Cryptocurrency Corner</Link>
          </Menu.Item>
        </Menu>
      </Header>
    </Layout>
  );
};

export default Navbar;
